.map {
  width: 90%;
  height: 90%;
}

svg.map {
  fill: #e3e3e3;
  stroke: #b0b0b0;
}

svg.map a:visited {
  fill: #e3e3e3;
}

svg.map a :hover {
  stroke-width: 5px;
  stroke-linejoin: round;
  stroke: #000;
}

