.map {
    width: 90%;
    height: 90%;
}

svg.map {
    fill: #e3e3e3;
    stroke: #b0b0b0;
}

svg.map a:visited {
    fill: #e3e3e3;
}

svg.map a :hover {
    stroke-width: 5px;
    stroke-linejoin: round;
    stroke: black;
}

.info {
    display: none;
}

body {
    width: 80%;
    margin: 0 auto;
    /* Center the content */
}

/* Media query for screens up to a certain width (e.g., 768px for typical tablets) */
@media screen and (max-width: 768px) {

    /* Mobile styling - body width set to 100% */
    body {
        width: 100%;
    }
}